import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "../../../../MembersTab/staff/Mainbody/PromoteDepartment/PromoteDepartment.module.css";
import ClassSubjectMenu from "./ClassSubjectMenu";
import SubjectCatalog from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/SubjectCatalog/SubjectCatalog";
import SubjectAttendance from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/Attendence/SubjectAttendance";
import SubjectTeacherAssignment from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/SubjectTeacherAssignment/SubjectTeacherAssignment";
import SubjectMarks from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/SubjectMarks/SubjectMarks";
import AssignmentDetailModal from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/SubjectTeacherAssignment/SelectStudentModal/AssignmentDetailModal";
import AssignmentStudentDetail from "../../../../MembersTab/staff/Mainbody/SubjectTeacher/SubjectTeacherAssignment/AssignmentStudentDetail";
const ClassSubject = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { t } = useTranslation();
  return (
    <div className={style.departmenthead}>
      <div className={style.demaprtment_list}>
        <div className={style.with_search}>
          {
            <h6>
              {" "}
              {t("subject_name")}
              {" : "}
              {getQuery.state?.subjectName ?? ""}
            </h6>
          }
        </div>
        {/* {searchParams.get("n") ? null : ( */}
        <BorderBottom
          customStyle={{
            width: "100%",
            marginTop: "0.5rem",
            marginBottom: "0.5rem",
          }}
        />
        {/* )} */}
        {searchParams.get("n") ? (
          searchParams.get("n") === "subject-catalog" ? (
            <SubjectCatalog
              cid={getQuery.state?.cid}
              bid={getQuery.state?.batchId}
              isDepartment="INSTITUTE_SUBJECT"
              sid={getQuery.state?.subjectId}
            />
          ) : searchParams.get("n") === "subject-attendance" ? (
            <SubjectAttendance
              sid={getQuery.state?.subjectId}
              bid={getQuery.state?.batchId}
              isDepartment="INSTITUTE_SUBJECT"
              subjectName={getQuery.state?.subjectName ?? ""}
            />
          ) : searchParams.get("n") === "subject-assignment" ? (
            <SubjectTeacherAssignment
              sid={getQuery.state?.subjectId}
              cid={getQuery.state?.cid}
              carryParentState={getQuery.state}
              bid={getQuery.state?.batchId}
              isDepartment="INSTITUTE_SUBJECT"
            />
          ) : searchParams.get("n") === "subject-exam" ? (
            <SubjectMarks
              sid={getQuery.state?.subjectId}
              cid={getQuery.state?.cid}
              isDepartment="INSTITUTE_SUBJECT"
            />
          ) : searchParams.get("n") === "assignment-detail" ? (
            <AssignmentDetailModal
              carryParentState={getQuery.state}
              isDepartment="INSTITUTE_SUBJECT"
            />
          ) : searchParams.get("n") === "assignment-student-detail" ? (
            <AssignmentStudentDetail isDepartment="INSTITUTE_SUBJECT" />
          ) : null
        ) : (
          <ClassSubjectMenu
            carryParentState={getQuery.state}
            // isDepartment="INSTITUTE_SUBJECT"
          />
        )}
      </div>
    </div>
  );
};

export default ClassSubject;
